// src/pages/IncomeGraph.js
import React, { useMemo, useState, useCallback } from "react";
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer,
  ReferenceLine,
} from "recharts";
import { simulateHousehold } from "./IncomeCalc";
import "../App.css";

// 
const WAN = 10000;
const toWan = (yen) =>
  isFinite(yen) ? Math.round(((yen || 0) / WAN) * 10) / 10 : 0;

// 
// 1/2UI
// 
function ConditionForm({
  title,
  state,
  setState,
  onChildAdd,
  onChildDel,
  onChildUpdate,
}) {
  const nset = (key) => (e) =>
    setState((s) => ({
      ...s,
      [key]: e.target.value === "" ? "" : Number(e.target.value),
    }));
  const sset = (key) => (e) =>
    setState((s) => ({
      ...s,
      [key]: e.target.value,
    }));

  return (
    <div className="card mb">
      <h2 className="mt0">{title}</h2>

      <div className="grid2">
        <div>
          <h3 className="mt0"></h3>
          <div className="row">
            <label></label>
            <input
              type="number"
              step={1}
              value={state.aAge}
              onChange={nset("aAge")}
            />
          </div>
          <div className="row">
            <label></label>
            <input
              type="number"
              step={1}
              value={state.bAge}
              onChange={nset("bAge")}
            />
          </div>
          <div className="row">
            <label></label>
            <select value={state.bDisability} onChange={sset("bDisability")}>
              <option value="none"></option>
              <option value="general"></option>
              <option value="special"></option>
            </select>
          </div>
        </div>

        <div>
          <h3 className="mt0"></h3>
          <div className="row">
            <label></label>
            <select value={state.mode} onChange={sset("mode")}>
              <option value="ratio"></option>
              <option value="fixedSpouse"></option>
            </select>
          </div>
          {state.mode === "ratio" ? (
            <div className="row">
              <label>%</label>
              <input
                type="number"
                min={0}
                max={100}
                step={1}
                value={state.ratioPct}
                onChange={nset("ratioPct")}
              />
            </div>
          ) : (
            <div className="row">
              <label>/</label>
              <input
                type="number"
                step={1}
                value={state.fixedSpouseWan}
                onChange={nset("fixedSpouseWan")}
              />
            </div>
          )}
        </div>
      </div>

      <div className="grid2">
        <div>
          <h4></h4>
          <div className="row">
            <label>/</label>
            <input
              type="number"
              step={0.1}
              value={state.bonusA}
              onChange={nset("bonusA")}
            />
          </div>
          <div className="row">
            <label>/</label>
            <input
              type="number"
              min={1}
              max={6}
              step={1}
              value={state.bonusTimesA}
              onChange={nset("bonusTimesA")}
            />
          </div>
        </div>
        <div>
          <h4></h4>
          <div className="row">
            <label>/</label>
            <input
              type="number"
              step={0.1}
              value={state.bonusB}
              onChange={nset("bonusB")}
            />
          </div>
          <div className="row">
            <label>/</label>
            <input
              type="number"
              min={1}
              max={6}
              step={1}
              value={state.bonusTimesB}
              onChange={nset("bonusTimesB")}
            />
          </div>
        </div>
      </div>

      <h3></h3>
      <div className="mb flex" style={{ gap: 8, flexWrap: "wrap" }}>
        <button type="button" onClick={onChildAdd}>
           
        </button>
        <button
          type="button"
          onClick={onChildDel}
          disabled={state.children.length === 0}
        >
           
        </button>
        <span className="muted">5</span>
      </div>
      <div className="scroll-x">
        <table className="table">
          <thead>
            <tr>
              <th>#</th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {state.children.map((c, idx) => (
              <tr key={idx}>
                <td>{idx + 1}</td>
                <td>
                  <input
                    type="number"
                    step={1}
                    value={c.age}
                    onChange={(e) => onChildUpdate(idx, "age", e.target.value)}
                  />
                </td>
                <td>
                  <input
                    type="number"
                    step={0.1}
                    value={c.incomeWan}
                    onChange={(e) =>
                      onChildUpdate(idx, "incomeWan", e.target.value)
                    }
                  />
                </td>
                <td>
                  <select
                    value={c.grade}
                    onChange={(e) =>
                      onChildUpdate(idx, "grade", e.target.value)
                    }
                  >
                    <option value="none"></option>
                    <option value="1">1=</option>
                    <option value="2">2=</option>
                  </select>
                </td>
                <td>
                  <input
                    type="checkbox"
                    checked={!!c.cohab}
                    onChange={(e) =>
                      onChildUpdate(idx, "cohab", e.target.checked)
                    }
                  />
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      <h3></h3>
      <div className="grid2">
        <div>
          <div className="row">
            <label></label>
            <input
              type="number"
              step={1000}
              value={state.lifeInsPaid}
              onChange={nset("lifeInsPaid")}
            />
          </div>
          <div className="row">
            <label></label>
            <input
              type="number"
              step={1000}
              value={state.quakeInsPaid}
              onChange={nset("quakeInsPaid")}
            />
          </div>
          <div className="row">
            <label></label>
            <input
              type="number"
              step={1000}
              value={state.smallKPaid}
              onChange={nset("smallKPaid")}
            />
          </div>
        </div>
        <div>
          <div className="row">
            <label></label>
            <input
              type="number"
              step={1000}
              value={state.medPaid}
              onChange={nset("medPaid")}
            />
          </div>
          <div className="row">
            <label></label>
            <input
              type="number"
              step={1000}
              value={state.medInsRec}
              onChange={nset("medInsRec")}
            />
          </div>
          <div className="row">
            <label></label>
            <input
              type="number"
              step={1000}
              value={state.donationPaid}
              onChange={nset("donationPaid")}
            />
          </div>
        </div>
      </div>
    </div>
  );
}

// 
// 
// 
export default function IncomeGraph() {
  // X
  const [xMax, setXMax] = useState(1500); // 01500
  const [step, setStep] = useState(10); // 10

  // 1
  const [cond1, setCond1] = useState({
    mode: "ratio", // ratio | fixedSpouse
    ratioPct: 50,
    fixedSpouseWan: 150,
    aAge: 35,
    bAge: 35,
    bDisability: "none", // none|general|special
    bonusA: 0,
    bonusTimesA: 2,
    bonusB: 0,
    bonusTimesB: 2,
    children: [],
    lifeInsPaid: 0,
    quakeInsPaid: 0,
    smallKPaid: 0,
    medPaid: 0,
    medInsRec: 0,
    donationPaid: 0,
  });

  // 2
  const [cond2, setCond2] = useState({
    mode: "ratio",
    ratioPct: 50,
    fixedSpouseWan: 150,
    aAge: 35,
    bAge: 35,
    bDisability: "none",
    bonusA: 0,
    bonusTimesA: 2,
    bonusB: 0,
    bonusTimesB: 2,
    children: [],
    lifeInsPaid: 0,
    quakeInsPaid: 0,
    smallKPaid: 0,
    medPaid: 0,
    medInsRec: 0,
    donationPaid: 0,
  });

  // 
  const nsetTop = (fn) => (e) =>
    fn(e.target.value === "" ? "" : Number(e.target.value));

  // /
  const splitByRatio = useCallback((totalWan, ratioPct) => {
    const aWan = Math.max(0, Math.round((totalWan * ratioPct) / 100));
    const bWan = Math.max(0, totalWan - aWan);
    return { aWan, bWan };
  }, []);

  // 1
  const buildRowsForCond = useCallback(
    (cond) => {
      const rows = [];
      for (let x = 0; x <= xMax; x += step) {
        let aWan = 0,
          bWan = 0;
        if (cond.mode === "ratio") {
          const { aWan: a, bWan: b } = splitByRatio(x, cond.ratioPct);
          aWan = a;
          bWan = b;
        } else {
          bWan = Math.max(0, Math.min(x, cond.fixedSpouseWan));
          aWan = Math.max(0, x - bWan);
        }

        const sim = simulateHousehold({
          aSalaryWan: aWan,
          aBonusWan: cond.bonusA,
          aBonusTimes: cond.bonusTimesA,
          bSalaryWan: bWan,
          bBonusWan: cond.bonusB,
          bBonusTimes: cond.bonusTimesB,
          aAge: cond.aAge,
          bAge: cond.bAge,
          children: cond.children,
          bDisability: cond.bDisability,
          lifeInsPaid: cond.lifeInsPaid,
          quakeInsPaid: cond.quakeInsPaid,
          smallKPaid: cond.smallKPaid,
          medPaid: cond.medPaid,
          medInsRec: cond.medInsRec,
          donationPaid: cond.donationPaid,
        });

        const hh = sim.household;
        const allow = sim.allowanceYear || 0;
        const bd = sim.allowanceBreakdown || {};

        rows.push({
          x,
          with: toWan(hh.net + allow),
          without: toWan(hh.net),
          allow: toWan(allow),
          tkj: toWan(bd.tokujifuYearly || 0),
          sj: toWan(bd.shojiYearly || 0),
          ts: toWan(bd.tokushoYearly || 0),
        });
      }
      return rows;
    },
    [xMax, step, splitByRatio]
  );

  const data1 = useMemo(() => buildRowsForCond(cond1), [cond1, buildRowsForCond]);
  const data2 = useMemo(() => buildRowsForCond(cond2), [cond2, buildRowsForCond]);

  // >0  0  x
  const findWall = (xs, key) => {
    for (let i = 1; i < xs.length; i++) {
      const prev = xs[i - 1][key] || 0;
      const cur = xs[i][key] || 0;
      if (prev > 0 && cur === 0) return xs[i].x;
    }
    return null;
  };
  const wall1 = {
    tkj: findWall(data1, "tkj"),
    sj: findWall(data1, "sj"),
    ts: findWall(data1, "ts"),
  };
  const wall2 = {
    tkj: findWall(data2, "tkj"),
    sj: findWall(data2, "sj"),
    ts: findWall(data2, "ts"),
  };

  // Recharts  data cond1  cond2 
  const mergedData = useMemo(() => {
    const byX2 = new Map(data2.map((r) => [r.x, r]));
    return data1.map((r1) => {
      const r2 = byX2.get(r1.x) || {};
      return {
        x: r1.x,
        y1_with: r1.with,
        y1_without: r1.without,
        y2_with: r2.with,
        y2_without: r2.without,
      };
    });
  }, [data1, data2]);

  // /
  const [clickedX, setClickedX] = useState(null);
  const handleChartClick = (state) => {
    if (state && state.activeLabel != null) setClickedX(state.activeLabel);
  };
  const row1 = clickedX != null ? data1.find((r) => r.x === clickedX) : null;
  const row2 = clickedX != null ? data2.find((r) => r.x === clickedX) : null;

  //  CSVcond1/cond2
  const exportRows = useMemo(() => {
    const byX2 = new Map(data2.map((r) => [r.x, r]));
    return data1.map((r1) => {
      const r2 = byX2.get(r1.x) || {};
      return {
        x: r1.x,
        // 1
        y1_with: r1.with,
        y1_without: r1.without,
        y1_allow: r1.allow,
        y1_tkj: r1.tkj,
        y1_sj: r1.sj,
        y1_ts: r1.ts,
        // 2
        y2_with: r2.with,
        y2_without: r2.without,
        y2_allow: r2.allow,
        y2_tkj: r2.tkj,
        y2_sj: r2.sj,
        y2_ts: r2.ts,
      };
    });
  }, [data1, data2]);

  function toCSV(rows) {
    const headers = [
      "x",
      "y1_with", "y1_without", "y1_allow", "y1_tkj", "y1_sj", "y1_ts",
      "y2_with", "y2_without", "y2_allow", "y2_tkj", "y2_sj", "y2_ts",
    ];
    const lines = [headers.join(",")];
    for (const r of rows) {
      lines.push(headers.map((h) => (r[h] ?? "")).join(","));
    }
    return "\uFEFF" + lines.join("\n"); // BOM
  }

  function downloadCSV() {
    const csv = toCSV(exportRows);
    const blob = new Blob([csv], { type: "text/csv;charset=utf-8;" });
    const url = URL.createObjectURL(blob);
    const a = document.createElement("a");
    const pad = (n) => String(n).padStart(2, "0");
    const d = new Date();
    a.href = url;
    a.download = `income_graph_${d.getFullYear()}${pad(
      d.getMonth() + 1
    )}${pad(d.getDate())}_${pad(d.getHours())}${pad(
      d.getMinutes()
    )}${pad(d.getSeconds())}.csv`;
    document.body.appendChild(a);
    a.click();
    a.remove();
    URL.revokeObjectURL(url);
  }

  return (
    <div className="App page">
      <h1 className="App-title"></h1>
      <p className="App-text">
        12<strong>UI</strong>/
      </p>

      {/* X */}
      <div className="card mb">
        <h2 className="mt0">X</h2>
        <div className="grid2">
          <div className="row">
            <label></label>
            <input
              type="number"
              step={10}
              value={xMax}
              onChange={nsetTop(setXMax)}
            />
          </div>
          <div className="row">
            <label></label>
            <input
              type="number"
              step={1}
              value={step}
              onChange={nsetTop(setStep)}
            />
          </div>
        </div>
      </div>

      {/* 1 */}
      <ConditionForm
        title="1"
        state={cond1}
        setState={setCond1}
        onChildAdd={() =>
          setCond1((s) =>
            s.children.length < 5
              ? {
                  ...s,
                  children: [
                    ...s.children,
                    { age: "", incomeWan: "", grade: "none", cohab: false },
                  ],
                }
              : s
          )
        }
        onChildDel={() =>
          setCond1((s) => ({ ...s, children: s.children.slice(0, -1) }))
        }
        onChildUpdate={(i, k, v) =>
          setCond1((s) => ({
            ...s,
            children: s.children.map((c, idx) =>
              idx === i ? { ...c, [k]: v } : c
            ),
          }))
        }
      />

      {/* 2 */}
      <ConditionForm
        title="2"
        state={cond2}
        setState={setCond2}
        onChildAdd={() =>
          setCond2((s) =>
            s.children.length < 5
              ? {
                  ...s,
                  children: [
                    ...s.children,
                    { age: "", incomeWan: "", grade: "none", cohab: false },
                  ],
                }
              : s
          )
        }
        onChildDel={() =>
          setCond2((s) => ({ ...s, children: s.children.slice(0, -1) }))
        }
        onChildUpdate={(i, k, v) =>
          setCond2((s) => ({
            ...s,
            children: s.children.map((c, idx) =>
              idx === i ? { ...c, [k]: v } : c
            ),
          }))
        }
      />

      {/*  */}
      <div className="card mb">
        <div
          className="flex"
          style={{ justifyContent: "space-between", alignItems: "center", gap: 12 }}
        >
          <h2 className="mt0" style={{ marginBottom: 0 }}>
            
          </h2>
          <button type="button" onClick={downloadCSV}>CSV</button>
        </div>

        <div style={{ width: "100%", height: 460 }}>
          <ResponsiveContainer>
            <LineChart
              data={mergedData}
              margin={{ top: 24, right: 24, left: 10, bottom: 12 }}
              onClick={handleChartClick}
            >
              <CartesianGrid strokeDasharray="3 3" />
              <XAxis
                dataKey="x"
                type="number"
                domain={[0, xMax]}
                tickCount={Math.min(16, Math.floor(xMax / step) + 1)}
                label={{
                  value: "",
                  position: "insideBottom",
                  offset: -5,
                }}
              />
              <YAxis
                domain={[0, "dataMax"]}
                label={{ value: "", angle: -90, position: "insideLeft" }}
              />
              <Tooltip labelFormatter={(label) => `${label} `} />
              <Legend />

              {/* 1 */}
              {wall1.tkj != null && (
                <ReferenceLine
                  x={wall1.tkj}
                  stroke="#d32f2f"
                  strokeDasharray="4 4"
                  label={{ value: "(1)", position: "top" }}
                />
              )}
              {wall1.sj != null && (
                <ReferenceLine
                  x={wall1.sj}
                  stroke="#f44336"
                  strokeDasharray="4 4"
                  label={{ value: "(1)", position: "top" }}
                />
              )}
              {wall1.ts != null && (
                <ReferenceLine
                  x={wall1.ts}
                  stroke="#ef9a9a"
                  strokeDasharray="4 4"
                  label={{ value: "(1)", position: "top" }}
                />
              )}

              {/* 2 */}
              {wall2.tkj != null && (
                <ReferenceLine
                  x={wall2.tkj}
                  stroke="#1976d2"
                  strokeDasharray="4 4"
                  label={{ value: "(2)", position: "top" }}
                />
              )}
              {wall2.sj != null && (
                <ReferenceLine
                  x={wall2.sj}
                  stroke="#2196f3"
                  strokeDasharray="4 4"
                  label={{ value: "(2)", position: "top" }}
                />
              )}
              {wall2.ts != null && (
                <ReferenceLine
                  x={wall2.ts}
                  stroke="#90caf9"
                  strokeDasharray="4 4"
                  label={{ value: "(2)", position: "top" }}
                />
              )}

              {/* 1= */}
              <Line
                type="monotone"
                dataKey="y1_with"
                name="1"
                dot={false}
                strokeWidth={2}
                stroke="#e91e63"
              />
              <Line
                type="monotone"
                dataKey="y1_without"
                name="1"
                dot={false}
                strokeWidth={2}
                strokeDasharray="6 6"
                stroke="#e91e63"
              />

              {/* 2= */}
              <Line
                type="monotone"
                dataKey="y2_with"
                name="2"
                dot={false}
                strokeWidth={2}
                stroke="#3f51b5"
              />
              <Line
                type="monotone"
                dataKey="y2_without"
                name="2"
                dot={false}
                strokeWidth={2}
                strokeDasharray="6 6"
                stroke="#3f51b5"
              />
            </LineChart>
          </ResponsiveContainer>
        </div>
        <div className="muted" style={{ marginTop: 8 }}>
          0X
          <code>simulateHousehold</code> /
        </div>
      </div>

      {/* / */}
      <div className="card">
        <h2 className="mt0"></h2>
        {clickedX == null && (
          <div className="muted">
             / 1/2
          </div>
        )}
        {clickedX != null && (
          <>
            <div>
              <b>{clickedX}</b> 
            </div>
            <div className="grid2">
              <div>
                <h4 className="mt0">1</h4>
                {row1 ? (
                  <ul className="muted">
                    <li>
                      <b>{row1.with?.toLocaleString()} </b>
                    </li>
                    <li>{row1.without?.toLocaleString()} </li>
                    <li>{row1.allow?.toLocaleString()} </li>
                    <li>  {row1.tkj?.toLocaleString()} </li>
                    <li>  {row1.sj?.toLocaleString()} </li>
                    <li>  {row1.ts?.toLocaleString()} </li>
                  </ul>
                ) : (
                  <div className="muted"></div>
                )}
              </div>
              <div>
                <h4 className="mt0">2</h4>
                {row2 ? (
                  <ul className="muted">
                    <li>
                      <b>{row2.with?.toLocaleString()} </b>
                    </li>
                    <li>{row2.without?.toLocaleString()} </li>
                    <li>{row2.allow?.toLocaleString()} </li>
                    <li>  {row2.tkj?.toLocaleString()} </li>
                    <li>  {row2.sj?.toLocaleString()} </li>
                    <li>  {row2.ts?.toLocaleString()} </li>
                  </ul>
                ) : (
                  <div className="muted"></div>
                )}
              </div>
            </div>
          </>
        )}
      </div>
    </div>
  );
}